@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap");

.chapters {
  width: 100vw;
  min-height: 100vh;
  background: linear-gradient(
    124.29deg,
    #7e82f8 -40.64%,
    rgba(198, 58, 248, 0.9) 119.87%
  );
}

.chapters {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chapters > h1 {
  margin-top: 1rem;
  margin-bottom: 1rem;
  background: #fcaa87;
  box-shadow: 2px 6px 12px 2px rgba(0, 0, 0, 0.18);
  border-radius: 8px;
  color: white;
  font-family: "Inter", sans-serif;
  padding: 20px 30vw;
  border-radius: 8px;
}

.chaptersContainer__top {
  margin: 1rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.chaptersContainer__top > p {
  margin: 0rem 1rem;
  font-weight: 600;
  font-family: "Inter", sans-serif;
  color: black;
  min-width: 15vw;
  text-align: center;
  background-color: white;
  border-radius: 10px;
  padding: 10px 30px;
}

.chaptersContainer__top > button {
  border: none;
  cursor: pointer;
  outline-width: 0ch;
  margin: 0rem 1rem;
  font-weight: 600;
  font-family: "Inter", sans-serif;
  color: black;
  min-width: 15vw;
  text-align: center;
  background-color: white;
  border-radius: 10px;
  padding: 10px 30px;
}

.chaptersContainer__middle {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
}

.chaptersContainer__middle > div {
  margin-top: 1rem;
  display: flex;
  align-items: center;
}

.chapters__row > .image {
  height: 45px;
  width: 45px;
  background: white;
  border-radius: 10px;
  cursor: pointer;
}

.chaptersContainer__middle > div > span {
  padding: 10px 20px;
  background-color: #fcaa87;
  border-radius: 9px;
  color: white;
}

.chaptersContainer__middle > div > img {
  width: 30px;
  height: 35px;
  border-radius: 9px;
  background: beige;
  object-fit: contain;
}

.chaptersContainer__middle > div > p {
  margin: 0rem 1rem;
  font-weight: 600;
  font-family: "Inter", sans-serif;
  color: black;
  letter-spacing: 1px;
  min-width: 15vw;
  text-align: center;
  background-color: white;
  border-radius: 10px;
  padding: 10px 30px;
  cursor: pointer;
}

.Modal {
  background-color: #bbb7b7;
}

.close-modal {
  position: absolute;
  padding: 6px 10px;
  font-size: 1.2rem;
  right: 1rem;
  top: 1.5rem;
  z-index: 1000;
  cursor: pointer;
}
.modalContent {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.modalContent__row {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}

.modalContent__row > input {
  outline-width: 0;
  padding: 5px 10px;
  border-radius: 9px;
  border: 1px solid gray;
}

.modalContent__row > label {
  margin-right: 10px;
}

.modalContent > button {
  padding: 10px 20px;
  border-radius: 9px;
  color: white;
  outline-width: 0;
  border: none;
  align-items: center;
  background-color: #fcaa87;
  width: 15vw;
  cursor: pointer;
}

.modalContent__row > div > div > button {
  margin-left: 1rem;
  padding: 10px 30px;
  margin-right: 1rem;
  cursor: pointer;
  border: none;
  outline-width: 0;
  display: flex;
  align-items: center;
  border-radius: 7px;
}

.modalContent__row > div > div > button i {
  margin-right: 5px;
}
